import React, { useEffect, useState } from "react";
import { axiosPost } from "../services/axiosService";
import { useSelector } from "react-redux";
import { State } from "../redux/Types";
import { useLocation } from "react-router-dom";
import Loader from "./loaders/Loader";

const OutgrowCalc = () => {
  const email = useSelector((state: State) => state.user.userEmail);
  const [reload, setReload] = useState<any>(true);
  const location = useLocation();
  const addons = new URLSearchParams(location.search).get("addon");

  useEffect(() => {
    const body = {
      email,
      plan_code: "plus_01",
      quantity: 1,
      ...(addons && { addon: true }),
    };
    // addons?.delete('addon');  
    axiosPost(`/payments/calcHostedPage`, body)
      .then((response) => {
        window.location.assign(response.data.url);
      })
      .catch((err) => {
        console.log(err);
        setReload(false);
      });
    
  }, [reload]);
  return <Loader />;
};

export default OutgrowCalc;
